<template>
    <div class="quest" style="min-height:85vh;">
        <div id="top"/>
        <div class="sub_menu">
            <el-row>
                <el-col :span="11"><div class="grid-content"></div></el-col>
                <el-col :span="2"><div class="grid-content">
                    <el-button type="info" round v-on:click="logout">logout</el-button>
                </div></el-col>
                <el-col :span="11"><div class="grid-content"></div></el-col>
            </el-row>
        </div>
        <div class="dire-quest" v-if="qActive === true && speedCheck === true">
            <h1>SBSOD</h1>
            <div class="block">
                <!-- このblockが、質問１つ分 -->
                <span class="demonstration">
                    Q1 質問1<br><!-- 質問項目 --> <!-- <br>で改行 -->
                    I am very good at giving directions. <br><!-- 質問内容(英語) -->
                    道順を教えるのが得意だ。<!-- 質問内容(日本語) -->
                </span><br>
                <font size="1">かなり当てはまる←--------------------------→全く当てはまらない</font><!-- ここは全問共通にする -->
                <el-rate v-model="q1" :icon-classes="iconClasses" void-icon-class="el-icon-help" :colors="colors" :max=7></el-rate><!-- ここは全問共通にする -->
            </div>
            <!-- これを押したら、calc関数が発動。計算＋結果画面が表示される -->
            <el-button type="primary" @click="calc" v-scroll-to="'#top'">回答</el-button>
        </div>
        <div class="webSpeed" v-if="speedCheck === false">
            <h2>ネットの速度が遅いです！ほかの環境でお試しください！！</h2>
        </div>
        <div class="result" v-if="qActive === false">
            <!-- 結果出力。必要に応じて、内容を変更すること -->
            <h2>SBSOD 結果はこちら↓</h2>
            数値が4.75を
            <div v-if="re > 4.75">超えたため、方向音痴ではありません</div>
            <div v-if="re <= 4.75">以下のため、方向音痴という結果になりました</div>
            <el-rate
                v-model="re" disabled show-score text-color="#ff9900" :max=7>
            </el-rate>
            <el-button type="primary" @click="goToTest">認知症検査に進む</el-button>
        </div>
    </div>
</template>

<script>
    import firebase from 'firebase'
    import axios from "axios"
    
    export default {
        data() {
            return {
                qActive: true,
                speedCheck: false,
                datasize: 322000,
                ed: 0, tm: 0, sp: 0,
                st: (new Date()).getTime(),
                //質問数に応じて、変数を増やす
                q1: null,
                re: null,//これを計算結果の変数にする
                colors: { 2: '#F7BA2A', 4: { value: '#FF9900', excluded: true }, 5: '#ff6347', 7: '#dc143c' },
                iconClasses: ['el-icon-help', 'el-icon-help', 'el-icon-help']
            }
        },
        methods: {
            //ボタン操作など、外部から呼び出して動作する関数を入れておく。
            calc(){ //ここで、ポチポチ押してもらった内容で計算をする
                this.re = this.q1 //ここで計算！！！！！！
                this.qActive =false //アンケートの入力完了ボタンが押されたら->結果表示に切り替わる
            },
            goToTest(){ //「認知症検査に進む」ボタンを押したら、認知症検査用のページに飛ぶ
                this.$router.push('/about').catch(()=>{}); //希望のページに飛ばすためのコード
            },
            logout: function(){ //一定時間操作されていなかったら、強制ログアウトする
                clearTimeout(this.timeoutTimer)
                this.logouotRequest(this.email)
                firebase.auth().signOut();
                clearTimeout(this.timeoutTimer)
                this.$router.push('/')
            },
            logouotRequest: async function(username){ //firebaseに、ログアウトの支持を飛ばす
                const params = new URLSearchParams();
                params.append("name",username);
                await axios.post("http://localhost:8040/logout", params)
                //await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/logout", params)
                alert("ログアウトしました")
            },
        },
        mounted: function(){ 
            //mountedの中には、methodsよりも先に処理してほしい内容を書く
            //ログインした後に、ユーザー（プレイヤー）のネット回線速度を計算。遅かったらアラートを出して、強制ログアウトさせる。
            this.ed = (new Date()).getTime()
            this.tm = (this.ed - this.st) / 1000
            this.sp = Math.floor(this.datasize /this. tm)
            if(this.sp > 5000000){
                this.speedCheck = true
            }else{
                alert("インターネット速度がかなり遅いです。\nほかの環境で再度お試しください")
                this.logout()
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- webページの表示関連のデザイン。文字サイズや色の指定 -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

.block{
    margin-top: 10px;
}

.el-button{
    margin-top: 20px;
}

.sub_menu{
  margin: 0em;
}
</style>
